* {
  box-sizing: border-box;
}

.importent {
  color: #ff2d2d;
}

.icon {
  fill: #023333;
}

body {
  margin: 0;
  padding: 0;
  font-family: MyriadPro, sans-serif;
}

.header {
  background-color: #f8f8f8;
}

.logo-wrapper {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.logo-wrapper .title {
  text-transform: uppercase;
  color: #023333;
  width: 100%;
  text-align: center;
  padding-left: 30px;
}

.logo img {
  width: 100px;
}

main form {
  width: 70%;
}

.modal-body img {
  margin-left: 5px;
  margin-right: 20px;
}

.partner {
  display: flex;
}

.partner .partner-item {
  cursor: pointer;
  align-items: center;
  text-decoration: none;
  display: flex;
}

.partner .partner-item img {
  margin-right: 10px;
}

.partner .partner-item .title {
  text-transform: uppercase;
  font-size: 14px;
  line-height: 1.3;
}

@media (max-width: 992px) {
  main form {
    width: 80%;
  }
}

@media (max-width: 576px) {
  .logo-wrapper {
    flex-direction: column;
    padding-left: 12px;
    padding-right: 12px;
  }

  .logo-wrapper .title {
    padding-top: 30px;
    padding-left: 0;
  }

  main .form-wrapper {
    padding-left: 25px;
    padding-right: 25px;
  }

  main form {
    width: 100%;
  }

  .partner {
    padding-left: 25px;
    padding-right: 25px;
  }
}

.form-control {
  width: 100%;
  height: 52px;
  background: #fff;
  border: 1px solid #b0bbc4;
  border-radius: 150px;
  padding: 10px 20px;
  font-size: 14px;
  transition: all .4s;
}

.form-textarea {
  width: 100%;
  min-height: 100px;
  background: #fff;
  border: 1px solid #b0bbc4;
  border-radius: 5px;
  padding: 20px;
  font-size: 14px;
  transition: all .4s;
}

.mb-3 {
  margin-top: 25px;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  [type="date"]::-webkit-calendar-picker-indicator {
    padding-top: 0;
  }
}

.is-hidden {
  display: block;
}

button {
  outline: none;
}

.toggle {
  color: orange;
}

.drive-category > label, .english-level > label {
  margin-right: 15px;
}

.titles {
  text-align: center;
}

.titles:not(:first-child) {
  margin-top: 70px;
}

.textarea-control {
  width: 100%;
  height: 100px;
  width: 100%;
  background: #fff;
  border: 1px solid #b0bbc4;
  border-radius: 10px;
  padding: 10px 20px;
  font-size: 14px;
  transition: all .4s;
}

.second-text {
  text-align: center;
  font-size: 12px;
}

